    import {
        mapState,
        mapMutations
    } from 'vuex'

    export default {
		data (){
			return{
				textlist:'hello天线宝宝,您已经有8个小时没有登录了!',
				user:{
					user_img:'../../static/img/user_img.jpg',
					user_name:'天线宝宝ooo',
					nologin_time:'8'
				},
				menulist:[
					{
						id:'set_background_img',
						name:'设置头像背景图片'
					},
					{
						id:'update_user',
						name:'修改个人信息'
					},
					{
						id:'update_pass',
						name:'修改密码'
					},
					{
						id:'bing_email',
						name:'绑定邮箱'
					},
					{
						id:'bing_qq',
						name:'绑定QQ'
					},
					{
						id:'app_edition',
						name:'软件版本'
					},
					{
						id:'logout',
						name:'退出'
					}
				]
			}
		},
        computed: {
            ...mapState(['hasLogin', 'forcedLogin'])
        },
        methods: {
            ...mapMutations(['logout']),
            bindLogin() {
                uni.navigateTo({
                    url: '../login/login',
                });
            },
            bindLogout() {
                this.logout();
